const { getBlockHeight, getBlockData } = require('../../apis/tzstats.service');

export default function getBlockHeadsActions(payload) {
  return dispatch => {
    getBlockHeight(payload, (blockHeightError, blockHeightResponse) => {
      if (blockHeightError) {
        dispatch({
          type: 'GET_BLOCKS_ERR',
          payload: blockHeightError
        });
      }
      getBlockData(
        { blockId: blockHeightResponse.height, ...payload },
        (blockDataError, blockDataResponse) => {
          if (blockDataError) {
            dispatch({
              type: 'GET_BLOCKS_ERR',
              payload: blockDataError
            });
          }
          dispatch({
            type: 'GET_BLOCKS',
            payload: blockDataResponse
          });
        }
      );
    });
  };
}
